.folio__navbar {
    display: flex;
    justify-content:  space-between;
    align-items: center;

    padding:  2rem 6rem;
}

.folio__navbar-links {
    flex: 1;
    display: flex;
    justify-content:  flex-start;
    align-items: center;
}

.folio__navbar-links_logo {
    margin-right: 2rem;
}

.folio__navbar-links_logo img{
    width: 62px;
    height: 62px;
}

.folio__navbar-links_container {
    display: flex;
    flex-direction: row;
}


.folio__navbar-links_container p,
.folio__navbar-menu_container p,
.folio__navbar-menu_container-links p
{
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;

    margin:  0 1rem;
    cursor: pointer;
}

.folio__navbar-menu {
    margin-left: 1rem;
    display: none;
    position: relative;
}

.folio__navbar-menu svg {
    cursor: pointer;
}

.folio__navbar-menu_container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;

    text-align: end;
    background: #fff;
    padding: 2rem;
    position: absolute;
    top: 40px;
    right: 0;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0px 0px 5px rgba(0,0, 0,0.2);
}

.folio__navbar-menu_container p {
    margin: 1rem 0;
}


@media  screen and (max-width: 1050px) {
    .folio__navbar-links_container {
        display: none;
    }

    .folio__navbar-menu {
        display: flex;
    }
}

@media  screen and (max-width: 700px) {
    .folio__navbar {
        padding: 2rem 4rem;
    }
}

@media  screen and (max-width: 550px) {
    .folio__navbar {
        padding: 2rem;
    }

    .folio__navbar-menu_container {
        top: 20px;
    }
}